

"use client";

import { useState } from 'react';
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useApplicants } from "@/context/applicants-context";
import { useAuth } from '@/context/auth-context';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from "@/components/ui/dialog";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Calendar } from "@/components/ui/calendar";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Loader2, CalendarIcon } from 'lucide-react';
import { format } from 'date-fns';
import { Applicant } from '@/lib/types';
import { cn } from '@/lib/utils';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';

const scheduleSchema = z.object({
  date: z.date({ required_error: "An interview date is required."}),
  time: z.string().min(1, "An interview time is required."),
  interviewer: z.string(), // No longer required, will be handled by round-robin
});

type ScheduleFormValues = z.infer<typeof scheduleSchema>;

interface ScheduleInterviewDialogProps {
  isOpen: boolean;
  onOpenChange: (open: boolean) => void;
  selectedApplicants: Applicant[];
}

export function ScheduleInterviewDialog({ isOpen, onOpenChange, selectedApplicants }: ScheduleInterviewDialogProps) {
  const { scheduleInterviews } = useApplicants();
  const { users } = useAuth();
  const [isLoading, setIsLoading] = useState(false);

  const form = useForm<ScheduleFormValues>({
    resolver: zodResolver(scheduleSchema),
  });
  
  const teachers = users.filter(u => u.roles.includes('teacher'));

  const onSubmit = async (data: ScheduleFormValues) => {
    setIsLoading(true);
    const applicantIds = selectedApplicants.map(a => a.id);
    const interviewDetails = {
        ...data,
        date: format(data.date, 'yyyy-MM-dd')
    }
    
    const success = await scheduleInterviews(applicantIds, interviewDetails);
    
    setIsLoading(false);
    if (success) {
      form.reset();
      onOpenChange(false);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Schedule Interview</DialogTitle>
          <DialogDescription>
            Select a date and time for the {selectedApplicants.length} selected applicant(s). Interviewers will be assigned in a round-robin fashion.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <FormField control={form.control} name="date" render={({ field }) => (
                <FormItem className="flex flex-col">
                    <FormLabel>Interview Date</FormLabel>
                    <Popover>
                        <PopoverTrigger asChild>
                            <FormControl>
                                <Button variant="outline" className={cn("w-full justify-start text-left font-normal", !field.value && "text-muted-foreground")}>
                                    <CalendarIcon className="mr-2 h-4 w-4" />
                                    {field.value ? format(field.value, "PPP") : <span>Pick a date</span>}
                                </Button>
                            </FormControl>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="start">
                            <Calendar mode="single" selected={field.value} onSelect={field.onChange} initialFocus />
                        </PopoverContent>
                    </Popover>
                    <FormMessage />
                </FormItem>
            )} />
            <FormField control={form.control} name="time" render={({ field }) => (
              <FormItem>
                <FormLabel>Interview Time</FormLabel>
                <FormControl><Input placeholder="e.g., 2:00 PM" {...field} /></FormControl>
                <FormMessage />
              </FormItem>
            )} />

            <DialogFooter>
              <Button type="submit" disabled={isLoading}>
                {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Schedule
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}


    
